import React, { useRef } from 'react';
import "../resources/index.css"
import imageOne from '../image/hobby.gif'
import imageTwo from '../image/android.gif'
import imageThree from '../image/react_moive.PNG'



function App() {
    const hobbyRef = useRef();
    const androidRef = useRef();
    const reactRef = useRef();
    const clickVal = useRef();
    
    const useClick = (e)=>{
        const val=e.target.innerText;
        if(val==="Spring"){
          
          let imgDiv = hobbyRef.current.children[0];
          let imgSpan = [hobbyRef.current.children[1],hobbyRef.current.children[2]];

          
          let imgContent = hobbyRef.current.children[0].children[0];
          let imgBtn =  hobbyRef.current.children[0].children[0].children[0];
          imgDiv.style.width="100vw";
          imgDiv.style.height="60vh";
          imgContent.style.width="60vw";
          imgContent.style.height="60vh";
          imgContent.style.backgroundImage=`url(${imageOne})`;
          for (const e of imgSpan)e.style.display="block";
          
          hobbyRef.current.style.width="100vw";
          hobbyRef.current.style.height="100vh";
          imgBtn.style.display="inline-block";

            


        }
        else if(val==="Android"){
          let imgDiv = androidRef.current.children[0];
          let imgSpan = androidRef.current.children[1];
          console.log(androidRef.current.children[0].children[0].children[0])
          let imgContent = androidRef.current.children[0].children[0];
          let imgBtn =  androidRef.current.children[0].children[0].children[0];
          imgDiv.style.width="30vw";
          imgDiv.style.height="60vh";
          imgContent.style.width="30vw";
          imgContent.style.height="100vh";
          imgContent.style.backgroundImage=`url(${imageTwo})`;
          imgSpan.style.display="flex";
          
          androidRef.current.style.width="100vw";
          androidRef.current.style.height="100vh";
          imgBtn.style.display="inline-block";

            

        }
        else if(val==="React"){
          let imgDiv = reactRef.current.children[0];
          let imgSpan = [reactRef.current.children[1],reactRef.current.children[2],reactRef.current.children[3]]
          
          let imgContent = reactRef.current.children[0].children[0];
          let imgBtn =  reactRef.current.children[0].children[0].children[0];
          imgDiv.style.width="100vw";
          imgDiv.style.height="60vh";
          imgContent.style.width="60vw";
          imgContent.style.height="60vh";
          imgContent.style.backgroundImage=`url(${imageThree})`;
          for(const e of imgSpan) e.style.display="block";
          
          
          reactRef.current.style.width="100vw";
          reactRef.current.style.height="100vh";
          imgBtn.style.display="inline-block";

            

        }
        else{
            alert("NotSearch VALUE")
            return ;
        }

        
    }
    const hobbyButton = (e)=>{
      let imgDiv = hobbyRef.current.children[0];
      let imgSpan = [hobbyRef.current.children[1],hobbyRef.current.children[2]];
      
      let imgContent = hobbyRef.current.children[0].children[0];
      let imgBtn =  hobbyRef.current.children[0].children[0].children[0];
      imgDiv.style.width="0vw";
      imgDiv.style.height="0vh";
      imgContent.style.width="0vw";
      imgContent.style.height="0vh";
      imgContent.style.backgroundImage=`none`;
      for (const e of imgSpan)e.style.display="none";
      
      hobbyRef.current.style.width="0vw";
      hobbyRef.current.style.height="0vh";
      imgBtn.style.display="none";
    }
    const androidButton = (e)=>{
      let imgDiv = androidRef.current.children[0];
      let imgSpan = androidRef.current.children[1];
      
      let imgContent = androidRef.current.children[0].children[0];
      let imgBtn =  androidRef.current.children[0].children[0].children[0];
      imgDiv.style.width="0vw";
      imgDiv.style.height="0vh";
      imgContent.style.width="0vw";
      imgContent.style.height="0vh";
      imgContent.style.backgroundImage=`none`;
      imgSpan.style.display="none";
      
      androidRef.current.style.width="0vw";
      androidRef.current.style.height="0vh";
      imgBtn.style.display="none";
    }

    const reactButton = (e)=>{
      let imgDiv = reactRef.current.children[0];
      let imgSpan = [reactRef.current.children[1],reactRef.current.children[2],reactRef.current.children[3]]
      
      let imgContent = reactRef.current.children[0].children[0];
      let imgBtn =  reactRef.current.children[0].children[0].children[0];
      imgDiv.style.width="0vw";
      imgDiv.style.height="0vh";
      imgContent.style.width="0vw";
      imgContent.style.height="0vh";
      imgContent.style.backgroundImage=`none`;
      for(const e of imgSpan) e.style.display="none";
      
      reactRef.current.style.width="0vw";
      reactRef.current.style.height="0vh";
      imgBtn.style.display="none";
    }
  return (
    <div className="App">
      <div className="image_hobby" ref={hobbyRef}>
          <div className="image-div">
            <div className="image-content">
              <div className="image-button" onClick={hobbyButton}>
                  <div className="button-block">  {/*버튼 이미지*/}
                          <button type="button">
                          <i className="mark x"></i>
                          <i className="mark xx"></i>
                          </button>
                  </div>
              </div>
            </div>
          </div>
          <span style={{display:"none"}}>기술 스택:JavaSpring,JavaTile,MVC,</span>
          <span style={{display:"none"}}> 취미 공유 사이트로 회원가입,수정,탈퇴,로그아웃등 기본적인 게시판 기능을 spring과 MVC패턴을 이용하여 구현함</span>
      </div>  
      <div className="image_android" ref={androidRef}>
          <div className="image-div">
            <div className="image-content">
              <div className="image-button" onClick={androidButton}>
                  <div className="button-block">  {/*버튼 이미지*/}
                          <button type="button">
                          <i className="mark x"></i>
                          <i className="mark xx"></i>
                          </button>
                  </div>
              </div>
            </div>
          </div>
          <div className="spanText">
            <span>기술 스택:Android(Kotlin),</span><br></br>
            <span> 벚꽃스팟알리미로 각 지역의 벚꽃명소를 리스트뷰로 보여주고 클릭시 검색기능을 구현하였고,<br></br> 벚꽃이미지 클릭시 개화기간 툴팁으로 표현</span>
          </div>
      </div>  
      
      <div className="image_react" ref={reactRef}>
          <div className="image-div">
            <div className="image-content">
              <div className="image-button" onClick={reactButton}>
                  <div className="button-block">  {/*버튼 이미지*/}
                          <button type="button">
                          <i className="mark x"></i>
                          <i className="mark xx"></i>
                          </button>
                  </div>
              </div>
            </div>
          </div>
          <span style={{display:"none"}}><a href="https://lso5507.github.io/movie_app-react-/#/" target="blink">LINK</a> </span>
          <span style={{display:"none"}}>기술 스택:React,axios</span>
          <span style={{display:"none"}}>영화 다운로드 사이트로 json을 axios로 파싱하여 Data를 출력 후 이미지 클릭시 Detail뷰로 이동합니다. </span>
          
      </div>    

      <div className="header">
        <h1>PORTFOLIO</h1>
      </div>
      <div className="content">
        <div  onClick={useClick}  ref={clickVal}>Spring</div>
        <div  onClick={useClick}  ref={clickVal}>Android</div>
        <div  onClick={useClick}  ref={clickVal}>React</div>
        <div style={{textAlign:"center"}} onClick={useClick}  ref={clickVal}>Door  (Loading..)</div>
        
      </div>
    </div>
  );
}

export default App;
